import React from 'react';
import Slider from "react-slick";
import { Popup, Card, Image, Rating } from 'semantic-ui-react'


export const SlickComp = () => {
    const product = [{
        name:'Meja Belajar Murah',
        harga: '300,000',
        pict: 'https://s1.bukalapak.com/img/63740775661/large/Capture_Frency.png.webp'
    },{
        name:'Meja Belajar Murah',
        harga: '300,000',
        pict: 'https://s1.bukalapak.com/img/63740775661/large/Capture_Frency.png.webp'
    },{
        name:'Meja Belajar Murah',
        harga: '300,000',
        pict: 'https://s1.bukalapak.com/img/63740775661/large/Capture_Frency.png.webp'
    },{
        name:'Meja Belajar Murah',
        harga: '300,000',
        pict: 'https://s1.bukalapak.com/img/63740775661/large/Capture_Frency.png.webp'
    }]
    const settings = {
        dots: true,
        infinite: false,
        slidesToShow: 5,
        slidesToScroll: 4,
        responsive: [
            {
            breakpoint: 1024,
            settings: {
                slidesToShow: 5,
                slidesToScroll: 4,
                infinite: true,
                dots: true
            }
            },
            {
            breakpoint: 600,
            settings: {
                slidesToShow: 2,
                slidesToScroll: 2,
                initialSlide: 2
            }
            },
            {
            breakpoint: 480,
            settings: {
                slidesToShow: 1,
                slidesToScroll: 1
            }
            }
        ]
      };
    return(
        <span>
            <Slider {...settings}>
            {product.map((row, index)=>{
                return(
                    <div key={index} className="px-3">
                        <Popup
                            trigger={
                            <Card className="p-0">
                                <Image src='https://s1.bukalapak.com/img/63740775661/large/Capture_Frency.png.webp' />
                                <Card.Content>
                                <Card.Header>{row.name}</Card.Header>
                                <Card.Description>
                                    Rp.300,000
                                </Card.Description>
                                </Card.Content>
                            </Card>
                            }
                        >
                            <Popup.Header>User Rating</Popup.Header>
                            <Popup.Content>
                            <Rating icon='star' defaultRating={3} maxRating={4} />
                            </Popup.Content>
                        </Popup>
                    </div>
                )
            })}
            </Slider>
        </span>
    )
}
